
.container{height:100%
}
.container .bg-content{position:relative;height:70%;background-image:url(../assets/index/bg-index.png);box-shadow:0px 4px 8px #ccc8c8
}
.container .bg-content img{width:100%;position:absolute;bottom:0
}
.container .upload-content{position:relative;height:30%
}
.container .upload-content .upload{background-color:#F5ABAC;border-radius:99px;width:5rem;height:5rem;position:absolute;top:0;left:0;right:0;bottom:0;margin:auto
}
.container .upload-content .upload .fa-icon{width:2.5rem;height:2.5rem
}



