
@keyframes fly-right{
0%{transform:translate(100%, 0%)
}
100%{transform:translate(0%, 0%)
}
}
@keyframes fly-left{
0%{opacity:0;transform:translate(-100%, 0%)
}
100%{opacity:1;transform:translate(0%, 0%)
}
}
@keyframes fly-down{
0%{opacity:0;transform:translate(0%, 100%)
}
100%{opacity:1;transform:translate(0%, 0%)
}
}
@keyframes fly-up-enter{
0%{opacity:0;transform:translate(0%, -100%)
}
100%{opacity:1;transform:translate(0%, 0%)
}
}
@keyframes fly-up-leave{
0%{opacity:1;transform:translate(0%, 0%)
}
100%{opacity:0;transform:translate(0%, -100%)
}
}
@keyframes fadeIn{
0%{opacity:0
}
100%{opacity:1
}
}
.bg-welcome{position:relative;width:100%;height:100%;background-image:url("../assets/welcome/bg.png");background-size:100% 100%;background-repeat:no-repeat
}
.bg-welcome .img{width:70%;position:absolute
}
.bg-welcome .img img{width:100%
}
.bg-welcome .img:nth-of-type(1){right:0;transform:translate(100%, 0)
}
.bg-welcome .img:nth-of-type(2){left:0;top:32%;transform:translate(-100%, 0%)
}
.bg-welcome .img:nth-of-type(3){bottom:0;right:0;transform:translate(0, 100%)
}
.bg-welcome .button{position:absolute;top:40px;left:.5rem;font-size:.6rem;width:30%;opacity:0
}
.bg-welcome .button.fadeIn{animation:fadeIn 1s ease forwards 4s
}
.flyRight{animation:fly-right 1s ease forwards 1s
}
.flyLeft{animation:fly-left 1s ease forwards 2s
}
.flyDown{animation:fly-down 1s ease forwards 3s
}



