
@keyframes fly-right{
0%{transform:translate(100%, 0%)
}
100%{transform:translate(0%, 0%)
}
}
@keyframes fly-left{
0%{opacity:0;transform:translate(-100%, 0%)
}
100%{opacity:1;transform:translate(0%, 0%)
}
}
@keyframes fly-down{
0%{opacity:0;transform:translate(0%, 100%)
}
100%{opacity:1;transform:translate(0%, 0%)
}
}
@keyframes fly-up-enter{
0%{opacity:0;transform:translate(0%, -100%)
}
100%{opacity:1;transform:translate(0%, 0%)
}
}
@keyframes fly-up-leave{
0%{opacity:1;transform:translate(0%, 0%)
}
100%{opacity:0;transform:translate(0%, -100%)
}
}
@keyframes fadeIn{
0%{opacity:0
}
100%{opacity:1
}
}
.edit-container{width:100%;height:100%
}
.edit-container .editimg{position:relative;background-image:url(../assets/index/bg-index.png);width:100%;height:70%
}
.edit-container .editimg .editing{width:100%;height:100%
}
.edit-container .editimg img{background-color:#fff;max-height:100%;width:100%;height:"";position:absolute;top:0;left:0;right:0;bottom:0;margin:auto
}
.edit-container .editimg .text{padding:2px 5px
}
.edit-container .edittool{position:relative;padding:5px 10px;height:15%
}
.edit-container .edittool .v-btn{position:relative;border-radius:99px;background-color:#F98A6A;width:2.5rem;height:2.5rem
}
.edit-container .edittool .back{position:relative;top:.6rem;width:2.5rem;height:2.5rem;border:none;background-color:#fff
}
.edit-container .edittool .back .fa-icon{color:#b9b6b6
}
.edit-container .edittool .fa-icon{width:1.25rem;height:1.25rem
}
.edit-container .edittool .undertext{font-size:.8rem
}
.edit-container .sumitcontainer{position:relative;width:100%;height:15%;text-align:center;padding:5% 0
}
.edit-container .preview{animation:fly-up-enter 1s ease forwards;width:100%;height:100%;background-color:rgba(0,0,0,0.8);position:absolute;top:0;z-index:999
}
.edit-container .preview img{width:100%;height:"";position:absolute;top:0;left:0;right:0;bottom:0;margin:auto
}
.edit-container .preview.hide{display:none
}
.edit-container .preview.leave{animation:fly-up-leave 1s ease forwards
}
.edit-container .preview .close{position:absolute;top:1rem;right:1.5rem
}
.edit-container .preview .close .fa-icon{width:1.5rem;height:1.5rem
}



