
.fa-icon {
  display: inline-block;
  fill: currentColor;
}
.fa-icon > path {
  transform-origin: 50% 50%;
}
.fa-flip-horizontal > path {
  transform: scale(-1, 1);
}
.fa-flip-vertical > path {
  transform: scale(1, -1);
}
.fa-spin > path {
  animation: fa-spin 1s 0s infinite linear;
}
.fa-inverse {
  color: #fff;
}
@keyframes fa-spin {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}



