
html,html body{width:100%;height:100%
}
html body{margin:0;padding:0
}
html body ul,html body li{margin:0;padding:0;list-style-type:none
}
html body .fade-enter-active{opacity:1;transition:all .5s
}
html body .fade-leave{color:#EADABA
}
html body .fade-enter{opacity:0;transition:all .5s
}
html body .fade-leave-active{opacity:0;transition:all .5s
}
html body .fa-icon{color:#FFF;width:"";height:"";position:absolute;top:0;left:0;right:0;bottom:0;margin:auto
}



